// src/features/main/MainPage.js
import React, { useEffect, useState } from "react";
import { useNavigate, useOutletContext } from "react-router-dom"; // ‚úÖ Outlet context ÏÇ¨Ïö©
import api from "../../api";
import "./MainPage.css";

const MAP_ROUTE = "/map";
const MANAGE_ROUTE = "/manage";

const PREVIEW_LIMIT = 8;
const SUMMARY_LIMIT = 12;

export default function MainPage() {
  const navigate = useNavigate();

  // ‚úÖ AppLayoutÏùò <Outlet context={{ user, setUser }}> Î°úÎ∂ÄÌÑ∞ user Î∞õÍ∏∞
  const outletCtx = useOutletContext();
  const user = outletCtx?.user ?? null;

  // Í∂åÌïú ÌåêÎ≥Ñ Ïú†Ìã∏: OWNER/ADMINÎßå ÌóàÏö©, REVIEWERÎäî Î∂àÍ∞Ä
  const canManageStores = (u) => {
    if (!u) return false;
    // Îã®Ïùº role ÌòπÏùÄ roles Î∞∞Ïó¥ Î™®Îëê ÎåÄÏùë
    const single = (u.role ? String(u.role) : "").toUpperCase();
    if (single) return single === "OWNER" || single === "ADMIN";
    const arr = Array.isArray(u.roles) ? u.roles.map((r) => String(r).toUpperCase()) : [];
    return arr.includes("OWNER") || arr.includes("ADMIN");
  };

  // ‚úÖ 'Îß§Ïû• Í¥ÄÎ¶¨' ÌÅ¥Î¶≠ Ìï∏Îì§Îü¨: Î°úÍ∑∏Ïù∏/Í∂åÌïú Ï≤¥ÌÅ¨
  const handleManageClick = () => {
    if (!user) {
      alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.");
      navigate("/login"); // Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄ Í≤ΩÎ°úÏóê ÎßûÍ≤å ÏàòÏ†ï Í∞ÄÎä•
      return;
    }
    if (!canManageStores(user)) {
      // ÏöîÏ≤≠: Î¶¨Î∑∞Ïñ¥Î©¥ Í∂åÌïú ÏóÜÏùå Î©îÏãúÏßÄ
      alert("Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§. (Î¶¨Î∑∞Ïñ¥Îäî Ï†ëÍ∑ºÌï† Ïàò ÏóÜÏäµÎãàÎã§.)");
      return;
    }
    navigate(MANAGE_ROUTE);
  };

  // 1) joinable ÎØ∏ÏÖò ‚Üí Îß§Ïû•Î≥Ñ ÎØ∏Î¶¨Î≥¥Í∏∞
  const [msLoading, setMsLoading] = useState(true);
  const [msError, setMsError] = useState("");
  const [missionStores, setMissionStores] = useState([]);

  // 2) Î™®Îì† Îß§Ïû• Ìïú Ï§Ñ ÏöîÏïΩ
  const [sumLoading, setSumLoading] = useState(true);
  const [sumError, setSumError] = useState("");
  const [summaries, setSummaries] = useState([]);

  useEffect(() => {
    let alive = true;
    (async () => {
      try {
        setMsLoading(true);
        setMsError("");

        const { data } = await api.get("/itda/missions/joinable");
        const list = Array.isArray(data) ? data : data?.items || [];

        const byStore = new Map();
        for (const m of list) {
          const s = m.store || {};
          const sid = s.id ?? m.storeId;
          if (!sid) continue;

          const poster =
            m.posterUrl || m.poster || m.imageUrl || m.thumbnailUrl || "";

          if (!byStore.has(sid)) {
            byStore.set(sid, {
              storeId: sid,
              name: s.name || m.storeName || "Îß§Ïû•",
              address: s.address || m.address || "",
              poster,
              count: 1,
            });
          } else {
            byStore.get(sid).count += 1;
          }
        }

        const items = Array.from(byStore.values())
          .sort((a, b) => b.count - a.count)
          .slice(0, PREVIEW_LIMIT);

        if (!alive) return;
        setMissionStores(items);
      } catch (e) {
        if (!alive) return;
        setMsError(e?.response?.data?.message || "ÎØ∏ÏÖò/Îß§Ïû• ÎØ∏Î¶¨Î≥¥Í∏∞Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
        setMissionStores([]);
      } finally {
        if (alive) setMsLoading(false);
      }
    })();
    return () => { alive = false; };
  }, []);

  useEffect(() => {
    let alive = true;
    (async () => {
      try {
        setSumLoading(true);
        setSumError("");

        const { data } = await api.get("/itda/stores");
        const stores = Array.isArray(data) ? data : data?.items || [];
        const pick = stores.slice(0, SUMMARY_LIMIT);

        if (pick.length === 0) {
          if (!alive) return;
          setSummaries([]);
          return;
        }

        const results = await Promise.allSettled(
          pick.map(async (s) => {
            const { data: sum } = await api.get(`/itda/stores/${s.id}/summary`);
            const text = typeof sum === "string" ? sum : (sum?.summary ?? "");
            return { id: s.id, name: s.name, summary: text };
          })
        );

        const ok = results
          .filter((r) => r.status === "fulfilled")
          .map((r) => r.value);

        if (!alive) return;
        setSummaries(ok);
      } catch (e) {
        if (!alive) return;
        setSumError(e?.response?.data?.message || "Îß§Ïû• ÏöîÏïΩÏùÑ Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
        setSummaries([]);
      } finally {
        if (alive) setSumLoading(false);
      }
    })();
    return () => { alive = false; };
  }, []);

  const hasMissionStores = missionStores.length > 0;
  const hasSummaries = summaries.length > 0;
  const fmtCount = (n) => (n > 99 ? "99+" : String(n || 0));

  return (
    <div className="main-wrap">
      <h2 className="main-title">Î¨¥ÏóáÏùÑ ÌïòÏãúÍ≤†Ïñ¥Ïöî?</h2>

      {/* ÏÉÅÎã®: Îëê Í∞úÏùò Ï£ºÏöî Ïï°ÏÖò Î≤ÑÌäº */}
      <div className="main-grid">
        <button
          type="button"
          className="main-card"
          onClick={() => navigate(MAP_ROUTE)}
          aria-label="ÎØ∏ÏÖò Ïù¥Î≤§Ìä∏ ÏßÄÎèÑ Î≥¥Í∏∞"
        >
          <div className="card-emoji" aria-hidden>üó∫Ô∏è</div>
          <div className="card-title">Itda Ïù¥Î≤§Ìä∏ Ï∞æÏïÑÎ≥¥Í∏∞!</div>
          <div className="card-desc">ÏßÑÌñâ Í∞ÄÎä•Ìïú ÎØ∏ÏÖòÏùÑ ÏßÄÎèÑÏóêÏÑú ÌôïÏù∏Ìï¥Ïöî.</div>
        </button>

        <button
          type="button"
          className="main-card"
          onClick={handleManageClick}  // ‚úÖ Í∂åÌïú Ï≤¥ÌÅ¨ÌïòÎäî Ìï∏Îì§Îü¨
          aria-label="ÎÇ¥ Îß§Ïû• Í¥ÄÎ¶¨Î°ú Ïù¥Îèô"
        >
          <div className="card-emoji" aria-hidden>üè™</div>
          <div className="card-title">Îß§Ïû• Í¥ÄÎ¶¨</div>
          <div className="card-desc">Îì±Î°ùÌïú Îß§Ïû•ÏùÑ ÌôïÏù∏ÌïòÍ≥† Ìé∏ÏßëÌï¥Ïöî.</div>
        </button>
      </div>

      {/* ÌïòÎã®: ÏúÑÏïÑÎûò ÏÑπÏÖò */}
      <div className="main-sections vertical">
        {/* ÏÑπÏÖò A: ÎØ∏ÏÖòÏù¥ Ïò¨ÎùºÏò® Îß§Ïû• ÎØ∏Î¶¨Î≥¥Í∏∞ */}
        <section className="home-section">
          <div className="section-header">
            <h3 className="section-title">ÎØ∏ÏÖòÏù¥ Ïò¨ÎùºÏò® Îß§Ïû•</h3>
            <button type="button" className="link-btn" onClick={() => navigate(MAP_ROUTE)}>
              Ï†ÑÏ≤¥ Î≥¥Í∏∞ ‚Üí
            </button>
          </div>

          {msLoading && <div className="section-empty">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>}
          {!msLoading && msError && <div className="section-error">{msError}</div>}
          {!msLoading && !msError && !hasMissionStores && (
            <div className="section-empty">ÌòÑÏû¨ ÏßÑÌñâ Í∞ÄÎä•Ìïú ÎØ∏ÏÖòÏù¥ ÏóÜÏäµÎãàÎã§.</div>
          )}

          {!msLoading && !msError && hasMissionStores && (
            <ul className="store-preview-grid">
              {missionStores.map((s) => (
                <li
                  key={s.storeId}
                  className="store-card"
                  title={s.name}
                  onClick={() => navigate(`${MAP_ROUTE}?storeId=${s.storeId}`)}
                >
                  <div className="store-thumb">
                    {s.poster ? (
                      <img src={s.poster} alt="Ìè¨Ïä§ÌÑ∞" />
                    ) : (
                      <div className="store-thumb-placeholder">Ìè¨Ïä§ÌÑ∞ ÏóÜÏùå</div>
                    )}
                    <span className="badge-count" aria-label={`ÏßÑÌñâÏ§ë ÎØ∏ÏÖò ${s.count}Í∞ú`}>
                      {fmtCount(s.count)}
                    </span>
                  </div>
                  <div className="store-body">
                    <div className="store-name">{s.name}</div>
                    {s.address && <div className="store-addr">{s.address}</div>}
                  </div>
                </li>
              ))}
            </ul>
          )}
        </section>

        {/* ÏÑπÏÖò B: Î™®Îì† Îß§Ïû•Ïùò Ìïú Ï§Ñ ÏöîÏïΩ */}
        <section className="home-section">
          <div className="section-header">
            <h3 className="section-title">Îß§Ïû•Ïùò Ìïú Ï§Ñ ÌèâÍ∞Ä!</h3>
          </div>

          {sumLoading && <div className="section-empty">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>}
          {!sumLoading && sumError && <div className="section-error">{sumError}</div>}
          {!sumLoading && !sumError && !hasSummaries && (
            <div className="section-empty">ÏöîÏïΩÏù¥ ÏóÜÏäµÎãàÎã§.</div>
          )}

          {!sumLoading && !sumError && hasSummaries && (
            <ul className="summary-list">
              {summaries.map((s) => (
                <li
                  key={s.id}
                  className="summary-item"
                  title={s.name}
                  onClick={() => navigate(`${MAP_ROUTE}?storeId=${s.id}`)}
                >
                  <div className="summary-name">{s.name}</div>
                  <div className="summary-text">
                    {s.summary || "ÏöîÏïΩÏù¥ Ï†úÍ≥µÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§."}
                  </div>
                </li>
              ))}
            </ul>
          )}
        </section>
      </div>
    </div>
  );
}
